<template>
  <div>
    <h3 class="news-title">{{this.$route.params.news[getIndex].title}}</h3>
    <span class="news-time">发布时间：{{$route.params.news[getIndex].time}}</span>
    <p class="news-content" v-html="content"></p>

    <div  class="page" @click="last" v-if="getIndex>0">上一个内容：{{this.$route.params.news[getIndex-1].title}}</div>
    <div  class="page" v-else>没有了</div>
    <div class="page" @click="next" v-if="getIndex<this.$route.params.news.length-1">下一个内容：{{this.$route.params.news[getIndex+1].title}}</div>
    <div  class="page" v-else>没有了</div>

    <div @click="back" class="back">返回</div>
  </div>
</template>
<script>
  export default {
    data(){
      return{
        getIndex:this.$route.params.index,
        content:this.$route.params.news[this.$route.params.index].text,
      }
    },
    methods:{
      back(){
        this.$router.go(-1)
      },
      last(){
        if(this.getIndex>0){
          this.getIndex--
          this.content=this.$route.params.news[this.getIndex].text
        }
      },
      next(){
        if(this.getIndex<this.$route.params.news.length-1){
          this.getIndex++
          this.content=this.$route.params.news[this.getIndex].text
        }
      }
    }
  }
</script>
<style>
  .news-content{
    width: 100%;
    margin: 0 auto;
    text-align: center;
    margin-top: 20px;
  }
.back{
 position: absolute;
  bottom:50px;
  font-size:20px;
  left: 40%;
  width: 50px;
  height: 50px;
  background: #c11920;
  text-align: center;
  line-height: 50px;
  color: #fff;
  border-radius: 10px;
}
  .page{ margin-top: 20px}
  h3.news-title{ font-size: 18px; margin: 12px;}
  span.news-time{ font-size: 12px; color: #cccccc;}
</style>



