<template>
  <div>
    <h3 class="news-title">{{$route.params.img1[$route.params.index].title}}</h3>
    <p class="txt2">{{$route.params.img1[$route.params.index].text}}</p>
    <div @click="back" class="back">返回</div>
  </div>
</template>
<script>
  export  default {
    data(){
      return{
        news:'111'
      }
    },
    methods:{
      back(){
        this.$router.go(-1)
      }
    }
  }
</script>
<style>
  .txt2{
    width: 100%;
    margin: 0 auto;
    text-align: center;
    margin-top: 20px;
  }
  .back{
    position: absolute;
    bottom:50px;
    font-size:20px;
    left: 40%;
    width: 50px;
    height: 50px;
    background: #c11920;
    text-align: center;
    line-height: 50px;
    color: #fff;
    border-radius: 10px;
  }
</style>



